import React, { useState, useEffect } from "react";
import { Form, ProgressBar, Alert } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  addwhatsuplink,
  listwhatsuplink,
  deletewhatsuplink,
} from "../../reduxx/actions/actions";

function Addwhatsup() {
  const dispatch = useDispatch();
  const [link, setLink] = useState("");
  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(addwhatsuplink({ link: link }));
  };
  useEffect(() => {
    dispatch(listwhatsuplink());
  }, []);
  const linklist = useSelector(
    ({ listwhatsuplink }) => listwhatsuplink.payload
  );
  return (
    <div>
      <div className="col-12 grid-margin stretch-card">
        <div className="card">
          <div className="card-body">
            <h4 className="card-title">WhatsApp</h4>
            <p className="card-description"> Add </p>
            <form className="forms-sample" onSubmit={handleSubmit}>
              <Form.Group>
                <label htmlFor="exampleInputName1">Whatsup Link</label>
                <Form.Control
                  type="text"
                  className="form-control"
                  id="exampleInputName1"
                  name="link"
                  onChange={(e) => setLink(e.target.value)}
                  placeholder="Enter Link"
                  required
                />
              </Form.Group>

              <button type="submit" className="btn btn-gradient-primary mr-2">
                Submit
              </button>
            </form>
            <p className="card-description mt-3"> List </p>
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th> S No </th>
                    <th> Link </th>
                    <th> status </th>
                    <th> Actions </th>
                  </tr>
                </thead>
                <tbody>
                  {Array.isArray(linklist) ? (
                    linklist.map((link, i) => {
                      return (
                        <tr>
                          <td> {i + 1}</td>
                          <td> {link.link} </td>
                          {
                            // status badge
                          }
                          {link.status === "INACTIVE" ? (
                            <td>
                              <span className="badge badge-danger">
                                {link.status}
                              </span>
                            </td>
                          ) : (
                            <td>
                              <span className="badge badge-success">
                                {link.status}
                              </span>
                            </td>
                          )}
                          <td>
                            <select
                              class="custom-select"
                              onChange={(event) => {
                                switch (event.target.value) {
                                  case "ACTIVE":
                                    dispatch(
                                      deletewhatsuplink({
                                        id: link.id + "",
                                        status: "ACTIVE",
                                      })
                                    );
                                    break;
                                  case "DEACTIVE":
                                    dispatch(
                                      deletewhatsuplink({
                                        id: link.id + "",
                                        status: "DEACTIVE",
                                      })
                                    );
                                    break;
                                  case "DELETE":
                                    dispatch(
                                      deletewhatsuplink({
                                        id: link.id + "",
                                        status: "DELETE",
                                      })
                                    );
                                    break;

                                  default:
                                    break;
                                }
                                event.target.value = "update_status";
                              }}
                            >
                              <option value="update_status">
                                Update Status
                              </option>
                              <option value="ACTIVE">ACTIVE</option>
                              <option value="DEACTIVE">INACTIVE</option>
                              <option value="DELETE">DELETE</option>
                            </select>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <tr>
                      <td
                        colSpan={5}
                        style={{ textAlign: "center", fontWeight: "bold" }}
                      >
                        <Alert className="p-3" variant="danger">
                          {" "}
                          No Data Found{" "}
                        </Alert>
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Addwhatsup;
